@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@300&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.landing-wrapper{position:fixed;background:#fffffff5;top:5rem;right:0;bottom:0;left:0;z-index:99999;display:flex;align-items:center;justify-content:center}.landing-wrapper .landing-container{position:absolute;background:#fff;height:300px;width:500px;border-radius:8px;padding:2rem;display:flex;grid-gap:2rem;gap:2rem;align-items:center;justify-content:center}.landing-wrapper .landing-container .landing-control{border:0;font-size:2rem;height:162px;padding:1rem 2rem;cursor:pointer;transition:all 0.7s ease;-webkit-animation:slide-bck-center 0.45s cubic-bezier(0.47, 0, 0.745, 0.715) both;animation:slide-bck-center 0.45s cubic-bezier(0.47, 0, 0.745, 0.715) both}.landing-wrapper .landing-container .landing-control.create-control{-webkit-clip-path:polygon(25% 0%, 100% 0%, 100% 100%, 25% 100%, 0% 50%);clip-path:polygon(25% 0%, 100% 0%, 100% 100%, 25% 100%, 0% 50%)}.landing-wrapper .landing-container .landing-control.sample-control{-webkit-clip-path:polygon(0% 0%, 75% 0%, 100% 50%, 75% 100%, 0% 100%);clip-path:polygon(0% 0%, 75% 0%, 100% 50%, 75% 100%, 0% 100%)}.landing-wrapper .landing-container .landing-control:hover{background-color:#795548;transition:all 0.7s ease;color:#fff}

