esume-init .e-company-wrapper .e-company-list .e-company-list-container .e-company-list-projects .e-company-list-project{border-bottom:1px dashed #cbc3c3;margin-bottom:1rem}#resume-init .e-company-wrapper .e-company-list .e-company-list-container .e-company-list-projects .e-company-list-project .e-company-list-project-name{font-size:1rem;font-weight:600;display:flex;justify-content:space-between}#resume-init .e-company-wrapper strong{font-size:14px}#resume-init .e-company-wrapper .company-heading{font-weight:800;background:#edeaea;margin-bottom:0.5rem;padding:0.5rem 0}#resume-init .details-section .p-details-field{display:flex;justify-content:space-between;max-width:392px}.resume-controls{margin-bottom:2rem;background:#f7f7f7;padding:0.5rem 9%;border-radius:2px 0 119px;border:2px solid #f1eded;border-top:0}.resume-controls .download-btn{border:0;padding:5px;font-size:14px}.resume-controls label{margin-bottom:inherit;margin-right:10px}

