#resume-init {
  padding: 1rem;
  border: 1px solid #ccc;
  max-width: 1024px;
  margin: 0 auto;

  label {
    font-weight: 700;
  }

  .download-controls {
    position: absolute;
    top: 1vh;
    right: 4vh;
  }

  .sub-title {
    font-size: 1.1rem;
    font-weight: 600;
    color: #c00001;
  }

  .section-heading {
    margin-top: 2rem;
  }

  .sub-child-title {
    font-size: 14px;
    font-weight: 600;
  }

  .custom-row {
    height: 5px;
    background-color: #c00001;
    margin: 1rem 0;
  }

  .each-section {
    margin-bottom: 1rem;
  }

  .personal-section-wrapper {
    .personal-section {
      display: flex;
      align-items: center;
      justify-content: space-between;

      .photo-container {
        .photo-avatar {
          clip-path: circle(73px at center);
          width: 150px;
          height: 150px;
        }
      }

      .p-details {
        &.p-name {
          font-size: 2rem;
          font-weight: 700;
          color: #c00001;
        }

        &.p-designation {
          font-size: 1.5rem;
        }
      }
    }
  }

  .skills-wrapper {
    list-style-type: none;
    display: flex;
    align-items: center;

    li {
      background-color: #646464;
      margin: 3px;
      padding: 2px 10px;
      font-size: 1rem;
      color: #fff;
    }
  }

  .e-company-wrapper {
    .e-company-list {
      .e-company-list-container {
        .e-company-list-details {
          margin-bottom: 1rem;

          .e-company-list-details-company-name {
            font-size: 1.3rem;
          }
        }

        .e-company-list-projects {
          .e-company-list-project {
            border-bottom: 1px dashed #cbc3c3;
            margin-bottom: 1rem;

            .e-company-list-project-name {
              font-size: 1rem;
              font-weight: 600;
              display: flex;
              justify-content: space-between;
            }
          }
        }
      }
    }

    strong {
      font-size: 14px;
    }

    .company-heading {
      font-weight: 800;
      background: #edeaea;
      margin-bottom: 0.5rem;
      padding: 0.5rem 0;
    }
  }

  .details-section {
    .p-details-field {
      display: flex;
      justify-content: space-between;
      max-width: 392px;

      label {
      }

      span {
      }
    }
  }
}
